.paginationWrapper {
  color: #f2f2f2;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.paginationBtn{
    color: #3242aa;
    margin: 2px;
    width: 30px;
    height: 30px;
    border: 3px solid #3242aa;
    box-shadow: 2px 3px 5px #ccc;
    background-color: transparent;
    border-radius: 50%;
}
.paginationBtn:hover{
    width: 35px;
    height: 35px;
}
.pagination {
  color: #3242aa;
  margin: 10px;
}
.prev, .next {
    color: #3242aa;
    margin: 10px;
    width: 40px;
    height: 40px;
    border: 3px solid #3242aa;
    box-shadow: 2px 3px 5px #ccc;
    background-color: transparent;
  }
  .prev:hover, .next:hover {
    color: #3242aa;
    width: 45px;
    height: 45px;
  }

